.sidebar-title {
  font-size: 22px;
  font-weight: normal;
  margin-bottom: 20px;
}
.sidebar {
  width: 15%;
  /* background: red; */
  position: fixed;
  height: 100vh;
  border-right: 2px solid #e5e5e5;
  z-index: 3;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo-container {
  margin-bottom: 4rem;
  margin-top: 1.5rem;
  font-size: 3rem;
}
.logo-container h1 {
  margin-top: 1.3rem;
}
